#logo{
    width: auto;
    float: left;
}
#logo img {
    width: auto;
    height: 40px;
    margin-top: 10px;
}

.menustyle{
	width: 100%;
    height: 60px;
    padding: 0 2.5%;
    margin: auto;
    background: #6ab04c;
    color: white;
    position: fixed;
    top: 0px;
    z-index: 1000;
}

.menustyle ul { width: auto;
    float: right;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 60px;
}

.menustyle ul li {
	list-style-type: none;
	float:left;
	text-transform: uppercase;
	color: white;
}

.menustyle ul li a {
    display: block;
    color: white;
    text-align: center;
    text-decoration: none;
    padding:5px 10px;
    font-size: 12px;
}

.menustyle ul li a:hover, a:active {
    color: #111;
}

@media (max-width: 320px){
    #nav_toggle  {display: -ms-flexbox;display: flex; float: right; margin-top: 20px;}
    #navbar {display: none;}
}

@media (min-width: 320px){
    #nav_toggle  {display: -ms-flexbox;display: flex; float: right; margin-top: 20px; padding: 0;}
    #navbar {display: none;}
}

@media (min-width: 769px){
    #nav_toggle {display: none;}
    #navbar {display: -ms-flexbox;display: flex;}
}



