.formStyle {
	width: 42%;
	height: 60px;
	float: right
}

.InputStyle {
	width: 164px;
	height: 22px;
	margin: 0px;
	border-radius: 0px
}

.LoginformStyle {
	height: 22px;
	margin: 0 8px;
	padding: 0 8px;
	font-size: 12px;
	text-align: center
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (max-width: 768px) {
    .formStyle {width: auto; float: right}
  } 


