#form{
	width: 520px;
    height: 400px;
    margin: 74px auto;
    padding: 30px 60px;
	background: #f3fafa;
	border-radius: 4px;
}

.label {
	width: 25%;
	margin-top: 6px;
	padding: 12px 12px 12px 0;
    display: block;
	float: left;
}

input[type=text], input[type=email], input[type=number] {
	width: 75%;
	height: 38px;
	padding: 4px 8px;
	margin: 10px 0px;
	border: 1px solid #58810F;
	border-radius: 4px;
	resize: none;
}

textarea {
	width: 75%;
	height: 100px;
	padding: 4px 8px;
	border: 1px solid #58810F;
	border-radius: 4px;
	resize: none;
}

input[type=submit] {
	margin:5px 4px;
	padding: 12px 32px;
	background-color: #45a049;
	color: white;
	border: none;
	border-radius: 4px;
	text-decoration: none;
	cursor: pointer;
	float: right;
}

@media screen and (max-width: 600px) {
	#form {
	  width: 90%;
	  padding: 30px 42px;
	}
}

@media screen and (max-width: 424px) {
	#form {
	  width: 90%;
	  height: 420px;
	  padding: 30px 42px;
	}
}



